/* Variables */
/* Mixins */
/* Global Styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1440px;
  background-color: #FFFFFF;
}

/* Image Styles */
img {
  width: 505px;
  height: 330px;
}

/* Main Content Styles */
h1 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.15rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
}

.desc {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 23.75em;
  height: 6em;
  margin-bottom: 2rem;
}

.btn {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 8rem;
  height: 3rem;
  background-color: #E4C1AE;
  border-radius: 3rem;
  border: none;
  color: #FFFFFF;
  cursor: pointer;
}

/* Modal Styles */
.modal {
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  width: 920px;
  height: 210px;
  background-color: #FFFFFF;
  opacity: 1;
  margin: 20% auto;
  padding-left: 6rem;
  animation-name: animateOpacity;
  animation-duration: 0.5s;
}
.modal-content .close {
  width: 1.5rem;
  height: 1.5rem;
  float: right;
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-right: 1.5rem;
  cursor: pointer;
}
.modal-content h2 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.15rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
  padding-top: 3.5rem;
}
.modal-content p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
}

@keyframes animateOpacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Layout Styles */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6rem;
  padding-top: 6rem;
}

